import React, { useEffect, useState } from "react";
import { DataType } from "../../interfaces/IDeviceInformation";
import { Table } from "antd";
import type { ColumnsType } from "antd/es/table";
import "./DeviceInfo.css";
import { DesktopOutlined, InfoCircleOutlined } from "@ant-design/icons";
import Dots from "../dots/dots";
import { dps, IDeviceAndBrowserSpecs } from "tnet-clickstream-connector";

dps.deviceAndBrowserSpecs.generateSpecs();

const DeviceInfo: React.FC = () => {
  // console.log(DeviceInformation());
  const [specs, setSpecs] = useState<DataType[]>([]);
  useEffect(() => {
    dps.deviceAndBrowserSpecs.$specs.subscribe((_specs) => {
      // const stableKeys = Object.keys(_specs?.stable);
      // var count = _specs?.stable.length;
      if (_specs) {
        const keyValueMapperCb = (
          [propKey, propValue]: [
            string,
            (
              | IDeviceAndBrowserSpecs["stable"]
              | IDeviceAndBrowserSpecs["unstable"]
            )
          ],
          stabilityStatus: string
        ) => ({
          key: propKey,
          property: `${propKey} (${stabilityStatus})`,
          details: JSON.stringify(propValue, (key, value) =>
            value === undefined ? "undefined" : value
          ),
        });

        const specs = Object.entries(_specs).flatMap(
          ([stabilityStatus, specsObject]: [
            string,
            (
              | IDeviceAndBrowserSpecs["stable"]
              | IDeviceAndBrowserSpecs["unstable"]
            )
          ]) =>
            Object.entries(specsObject).map(([key, value]) =>
              keyValueMapperCb([key, value], stabilityStatus)
            )
        );
        setSpecs(specs);
      }
    });
  }, []);

  const rowClassName = (record: DataType) => {
    const isStable = record?.property?.includes("(stable)") ? true : false;

    return isStable ? "stable-row" : "unstable-row";
  };

  const columns: ColumnsType<DataType> = [
    {
      title: (
        <div>
          <DesktopOutlined className="custom-icon" />
          Device Properties
          <div className="title-span">({specs.length} properties)</div>
        </div>
      ),
      dataIndex: "property",
      key: "property",
      filters: [
        {
          text: "stable",
          value: "stable",
        },
        {
          text: "unstable",
          value: "unstable",
        },
      ],
      //@ts-ignore
      onFilter: (
        value: string | number | boolean,
        record: DataType | undefined
      ) => {
        if (record?.property.includes(`(${value})`)) return record;
      },
    },
    {
      title: (
        <span>
          <InfoCircleOutlined className="custom-icon" />
          Details
        </span>
      ),
      dataIndex: "details",
      key: "details",
    },
  ];

  const mappedData = specs.map((record) => ({
    ...record,
    rowClassName: rowClassName(record),
  }));

  return (
    <>
      <Table
        dataSource={mappedData}
        columns={columns}
        rowClassName={rowClassName}
        className="flex"
        pagination={{ pageSize: 15 }}
      />
      <Dots />
    </>
  );
};

export default DeviceInfo;
