import React, { useRef, useEffect, useState } from "react";
import styles from "./dots.module.css";

const Dots = () => {
  const centeredButtonRef = useRef(null);
  const topLeftButtonRef = useRef(null);
  const topRightButtonRef = useRef(null);
  const bottomLeftButtonRef = useRef(null);
  const bottomRightButtonRef = useRef(null);

  const [centeredButtonCoordinates, setCenteredButtonCoordinates] = useState({
    x: 0,
    y: 0,
  });
  const [topLeftButtonCoordinates, setTopLeftButtonCoordinates] = useState({
    x: 0,
    y: 0,
  });
  const [topRightButtonCoordinates, setTopRightButtonCoordinates] = useState({
    x: 0,
    y: 0,
  });

  const [bottomLeftButtonCoordinates, setBottomLeftButtonCoordinates] =
    useState({
      x: 0,
      y: 0,
    });

  const [bottomRightButtonCoordinates, setBottomRightButtonCoordinates] =
    useState({
      x: 0,
      y: 0,
    });

  const logButtonCoordinates = () => {
    if (centeredButtonRef.current) {
      // @ts-ignore
      const buttonRect = centeredButtonRef.current.getBoundingClientRect();
      const { x, y } = buttonRect;
      setCenteredButtonCoordinates({ x, y });
      console.log("Centered Button Coordinates:", {
        x,
        y,
      });
    }

    if (topLeftButtonRef.current) {
      // @ts-ignore
      const buttonRect = topLeftButtonRef.current.getBoundingClientRect();
      const { x, y } = buttonRect;
      setTopLeftButtonCoordinates({ x, y });
      console.log("Top Left Button Coordinates:", {
        x,
        y,
      });
    }

    if (topRightButtonRef.current) {
      // @ts-ignore
      const buttonRect = topRightButtonRef.current.getBoundingClientRect();
      const { x, y } = buttonRect;
      setTopRightButtonCoordinates({ x, y });
      console.log("Top Right Button Coordinates:", {
        x,
        y,
      });
    }

    if (bottomLeftButtonRef.current) {
      // @ts-ignore
      const buttonRect = bottomLeftButtonRef.current.getBoundingClientRect();
      const { x, y } = buttonRect;
      setBottomLeftButtonCoordinates({ x, y });

      console.log("Bottom Left Button Coordinates:", {
        x,
        y,
      });
    }

    if (bottomRightButtonRef.current) {
      // @ts-ignore
      const buttonRect = bottomRightButtonRef.current.getBoundingClientRect();
      const { x, y } = buttonRect;
      setBottomRightButtonCoordinates({ x, y });
      console.log("Bottom Right Button Coordinates:", {
        x,
        y,
      });
    }
  };

  useEffect(() => {
    logButtonCoordinates();

    const handleResize = () => {
      logButtonCoordinates();
    };

    window.addEventListener("resize", handleResize);

    return () => {
      window.removeEventListener("resize", handleResize);
    };
  }, []);

  return (
    <div
      style={{
        display: "flex",
        justifyContent: "center",
      }}
    >
      <div ref={topLeftButtonRef} className={styles.topLeft}></div>
      <div ref={topRightButtonRef} className={styles.topRight}></div>
      <div ref={bottomLeftButtonRef} className={styles.bottomLeft}></div>
      <div ref={bottomRightButtonRef} className={styles.bottomRight}></div>
      <div ref={centeredButtonRef} className={styles.centered}></div>
    </div>
  );
};

export default Dots;
